import React, {useEffect, useState, useRef} from 'react';

import {useSelector, useDispatch} from "react-redux";
import {useHistory} from "react-router-dom";
import {CircularProgress, Typography} from "@mui/material";
import {makeStyles} from '@mui/styles';

import {BlockBox, Search, Ð¡ontrolBlock} from "../../components/common";
import {useAppSelector} from "../../hooks/redux";
import {callsSlice, getBaseCallsData} from "../../store/calls/calls.slice";
import {searchSlice} from '../../store/search/search.slice';
import {CallType} from "../../store/calls/calls.types";
import CallStubMiddleware from "./Call";
import {RootState} from "../../store/store";
import {translate} from "../../localizations";
import CallsHeader from './CallsHeader';
import {langSlice} from "../../store/lang/lang.slice";
import InfiniteScroll from 'react-infinite-scroll-component';

const Calls = React.memo(() => {
  const dispatch = useDispatch();
  const history = useHistory();

  const calls = useAppSelector<CallType[][]>(state => state.calls.calls);

  const [callsLocal, setCallsLocal] = useState<CallType[][]>([]);

  useEffect(() => {
    if (callsLocal.length < 1) {
      setCallsLocal(calls);
    } else {
      setCallsLocal(calls);
    }
  }, [calls]);
  // const {language} = useAppSelector((state: RootState) => state.lang);

  const [hasMore, setHasMore] = useState(true);
  const [expanded, setExpanded] = React.useState<string | false>(false);
  const handleExpandedChange = (callId: string | false) => {
    setExpanded(callId);
  };

  const pushNewCalls = async () => {
    setHasMore(false);
    await dispatch(getBaseCallsData({}));
    setHasMore(true);
  };

  useEffect(() => {
    document.title = "ÐÐ²Ð¾Ð½ÐºÐ¸ | IMOT.io";
    dispatch(langSlice.actions.setLoading(false));
    if (calls.length < 2) {
      pushNewCalls().then();
    }
    return () => {
      dispatch(searchSlice.actions.setDefaultCriterias(null));
    }
  }, []);

  useEffect(() => {
    return history.listen((location) => {
      if (location.pathname !== '/calls') {
        dispatch(callsSlice.actions.setEmptyState({leaveBundles: 1}));
      }
    });
  }, []);

  return (
    <div style={{cursor: 'default'}}>
      <Ð¡ontrolBlock/>
      <Search pageName="ÐÐ²Ð¾Ð½Ð¾Ðº"/>
      <BlockBox>
        <CallsHeader switchTitleFound={false}/>

        <div>
          <InfiniteScroll
            style={{overflow: "hidden !important"}}
            dataLength={callsLocal.length}
            next={pushNewCalls}
            hasMore={hasMore}
            loader={<CircularProgress color="primary"/>}
          >
            {callsLocal.length !== 0 ?
              callsLocal.map((callsArrays: CallType[]) => {
                return (
                  <div>
                    {callsArrays.map((call: CallType) => {
                      return (
                        <div>
                          <CallStubMiddleware
                            callInfo={call.info}
                            callAudio={call.audio}
                            callStt={call.stt}
                            bundleIndex={callsLocal.indexOf(callsArrays)}
                            expanded={expanded === call.info?.id}
                            handleExpandedChange={handleExpandedChange}
                          />
                        </div>
                      )
                    })}
                  </div>
                )
              })
              :
              <BlockBox padding={'24px'}><Typography>{translate('callsEmpty', "ru")}</Typography></BlockBox>
            }
          </InfiniteScroll>
        </div>
      </BlockBox>
    </div>
  );
});

export default Calls
